import React from 'react';
import { Table, Button } from 'react-bootstrap';
import { connect, useDispatch, useSelector } from 'react-redux';


function Cart(props) {

    let state = useSelector((state)=>state);
    let dispatch = useDispatch()

    return(
        <div>
            <Table striped bordered hover >
            <thead>
                <tr>
                    <th>#</th>
                    <th>상품명</th>
                    <th>수량</th>
                    <th>변경</th>
                </tr>

              </thead>
            <tbody>


                { 
                    //props.newProps.map((a,i)=>{
                    state.reducer.map((a,i)=>{
                        return (
                            <tr key={i}>
                                <td>{a.id  }</td>
                                <td>{a.name}</td>
                                <td>{a.quan}</td>
                                <td><Button variant="success" onClick={()=>{ dispatch({type: '수량증가', i : a.id}) }}> + </Button>
                                <Button variant="danger" onClick={()=>{ dispatch({type: '수량감소', i : a.id}) }}> - </Button></td>
                            </tr>
                            )
                        })  
                }
               
            </tbody>
            </Table>
            { state.reducer2 === true
                ? (<div className="my-alert2">
                <p>지금 구매하시면 신규할인 20%</p>
                <button onClick={()=>{dispatch({type : 'alert닫기'})}}>닫기</button>
                </div>)
                : null
            }
            { state.reducer2 === false
                ? (
                <button onClick={()=>{dispatch({type : 'alert닫기'})}}>다시 열어줘</button>
                )
                : null
            }
        </div>
    )
}

// function stateprops(state){
    
//     return{
//         newProps : state.reducer,
//         alert열렸니 : state.reducer2
//     }
     
// }

// export default connect(stateprops)(Cart);

export default Cart;